$headerColor: #333333;
$hexColor: #00cc99;
$stitleColor: #9b9b9b;
$questColor: #323223;
$ansColor: #a3a3a3;
$greybgColor: #ebebeb;

* {
  font-family: 微軟正黑體;
  // border: 1px solid black!important
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

html,body {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  margin: 0;
  padding: 0; }

.wrap {
  max-width: 960px;
  height: 60px; }

.header {
  background-color: $headerColor;
  z-index: {}
  h1 {
    float: left;
    a {
      margin-left: 10px;
      width: 174px;
      height: 50px;
      display: block;
      background: url(../image/logo-grey.png);
      background-repeat: no-repeat;
      text-indent: 101%;
      overflow: hidden;
      word-spacing: nowrap; } }
  .title {
    display: inline-block;
    padding-left: 15px; }
  .link {
    float: right;
    margin-top: 30px;
    a {
      font-size: 14px;
      padding: 10px;
      margin-right: 30px;
      text-decoration: none;
      letter-spacing: 2px;
      font-size: 18px;
      color: white;
      &:hover {
        color: $hexColor;
        border-bottom: 2px solid $hexColor; } } } }
.banner {
  background-image:url(../image/indexbanner.png) {}
  background-size: cover;
  background-position: center center;
  padding: 180px 20px;
  background-repeat: no-repeat;
  margin-bottom: 10px;
  .comeText {
    padding: 10px;
    h2 {
      font-size: 36px;
      color: $hexColor; }
    span {
      color: white; }
    h2 {
      margin: 0;
      margin-top: 5px; }
    .button {
      background-color: $hexColor;
      display: inline-block;
      padding: 12px 32px;
      margin-top: 10px;
      border-radius: 5px;
      color: white;
      cursor: pointer;
      &:hover {
        background-color: #4460a0; } } } }
.intro {
  padding: 15px;
  .top {
    font-size: 24px;
    .title {
      margin: 0;
      color: $hexColor;
      span {
        font-size: 16px;
        color: #222; } }
    .button {
      float: right;
      border: 1px solid $hexColor;
      margin-top: 15px;
      padding: 8px 12px;
      border-radius: 5px;
      font-size: 16px;
      cursor: pointer;
      &:hover {
        background-color: $hexColor;
        color: white; } } }
  .cards {
    padding: 20px;
    margin-bottom: 15px;
    display: flex;
    justify-content: space-around;
    width: 100%;
    img {
      width: 100%; }
    .card {
      width: 30%;
      border-radius: 5px;
      box-shadow: 0px 0px 10px rgba(black,0.5);
      h2 {
        text-align: center;
        font-weight: bold;
        color: #222; }
      p {
        font-size: 16px;
        padding-left: 8px;
        letter-spacing: 2px; } } } }
.content {
  padding: 15px;
  font-size: 24px;
  .title {
    display: inline-block;
    margin: 0;
    color: $hexColor;
    span {
      font-size: 16px;
      color: #222; } }
  .compare {
    font-size: 16px;
    display: flex;
    justify-content: center;
    margin-top: 20px;
    margin-bottom: 15px;
    text-align: left;
    th {
      padding: 10px 120px 10px 0px;
      text-align-last: left;
      border-top: 1px solid #000;
      .bottom {
        border-bottom: 1px solid #000; } }
    .head {
      font: bold;
      background-color: #fff; }
    .great {
      color: $hexColor; } } }
.subscribe {
  background-color: #ebebeb;
  display: flex;
  justify-content: space-around;
  align-items: flex-start;
  padding-bottom: 50px;
  .text {
    margin: 50px;
    font-weight: 800;
    display: inline-block;
    h2 {
      font-weight: 500;
      color: #323223;
      margin: 0;
      margin-bottom: 8px; } }
  .fill {
    width: 50%;
    margin: 50px 0px; }
  input {
    width: 90%;
    padding: 10px;
    margin-bottom: 20px;
    font-size: 18px;
    border-radius: 10px;
    border: none;
    background-color: white; }
  button {
    border: none;
    background-color: $hexColor;
    font-size: 16px;
    float: right;
    color: white;
    letter-spacing: 8px;
    padding: 12px 32px;
    margin-right: 50px;
    border-radius: 10px;
    cursor: pointer;
    &:hover {
      background-color: #4460a0; } } }

.footer {
  width: 100%;
  padding: 15px;
  padding-left: 20px;
  align-items: center;
  display: flex;
  justify-content: space-between;
  background-color: #323228;
  clear: both;
  h2 {
    width: 174px;
    height: 45px;
    background: url(../image/logo-grey.png);
    background-repeat: no-repeat;
    text-indent: 101%;
    overflow: hidden;
    word-spacing: nowrap; }
  .icon {
    float: left;
    margin: 10px;
    border-radius: 50%; } }

.faq {
  .banner {
    background-image: url(../image/qabanner.png);
    background-size: cover;
    background-position: center center;
    padding: 180px 20px;
    background-repeat: no-repeat;
    margin-bottom: 10px;
    .comeText {
      text-align: center;
      letter-spacing: 2px;
      h2 {
        color: white;
        font-size: 40px; }
      h3 {
        color: $hexColor;
        font-size: 26px; }
      input {
        border: none;
        width: 60%;
        padding: 8px;
        border-radius: 8px; } } }

  .intro {
    width: 95%;
    margin: 0 auto;
    .questions {
      padding: 20px;
      margin-bottom: 15px;
      letter-spacing: 1px;
      .top {
        margin-bottom: 8px;
        .title {
          color: $hexColor;
          span {
            font-size: 16px;
            colot: $stitleColor; } } }
      .quest {
        margin-bottom: 8px;
        .title {
          font-size: 20px;
          color: $questColor;
          margin-bottom: 4px; }
        .a {
          color: $ansColor;
          font-size: 18px; }
        .a4 {
          color: $questColor; }
        span {
          color: $hexColor; }
        ol,ul {
          margin: 0; } } } }
  .subscribe {
    .fill {
      textarea {
        border: none;
        width: 90%;
        border-radius: 8px;
        margin-bottom: 8px;
        letter-spacing: 2px;
        font-size: 18px;
        padding: 5px; } } } }

.course {
  .banner {
    background: url(../image/course.png);
    background-size: cover;
    padding: 180px 20px;
    background-repeat: no-repeat;
    margin-bottom: 10px;
    .comeText {
      h2 {
        color: white; }
      h3 {
        color: $hexColor;
        margin: 0;
        font-size: 24px; } } }
  .intro {
    width: 80%;
    margin: 0 auto;
    padding: 20px;
    .teacher {
      display: flex;
      margin: 10px 0px;
      .pic {
        img {
          border-radius: 50%; } }
      .intrdetail {
        letter-spacing: 2px;
        display: flex;
        align-items: flex-start;
        .left {
          margin: 30px;
          font-size: 36px;
          .name {
            color: $hexColor; }
          span {
            margin-left: 8px; } }
        ul {
          margin: 30px;
          font-size: 18px;
          li {
            margin-bottom: 8px;
            color: $ansColor; } } } }
    .absolute {
      letter-spacing: 2px;
      .top {
        .title {
          span {
            font-size: 20px; } } }
      .bottom {
        font-size: 18px;
        li {
          margin-bottom: 10px;
          line-height: 24px; } } }
    .outline {
      .top {
        span {
          font-size: 20px; } }
      .bottom {
        list-style: none;
        font-size: 18px;
        li {
          letter-spacing: 1px;
          padding: 15px 20px;
          border-radius: 8px; }
        .grey {
          background-color: $greybgColor; } } } }
  .subscribe {
    display: flex;
    align-items: flex-end;
    padding: 100px;
    .fill {
      textarea {
        border: none;
        width: 90%;
        border-radius: 8px;
        margin-bottom: 8px;
        letter-spacing: 2px;
        font-size: 18px;
        padding: 5px; } } } }


